  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  overflow-anchor: none;
  margin: 0;
  padding: 0;
  padding-top: 8rem;
  font-family: sans-serif;
  font-weight: 400;
  color: var(--black-color);
  background-color: var(--white-color);
  min-height: 200vh;
}
@media (max-width: 61.9375em) {
  body {
    padding-top: 6.75rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea {
  padding: 0;
  font: inherit;
  color: inherit;
  background-color: transparent;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

:root {
  --white-color: #fff;
  --black-color: #000;
  --primary-color: #17696a;
  --primary-hover-color: #145c5d;
  --primary-hover-color2: rgb(23 105 106 / 12%);
  --success-color: #03cea5;
  --danger-color: #ff4242;
  --warning-color: #f89828;
  --info-color: #5a87fc;
  --background-color: #e5e5e5;
  --gray900-color: #1e212c;
  --gray800-color: #424551;
  --gray700-color: #787a80;
  --gray600-color: #9a9ca5;
  --gray500-color: #b3b7bc;
  --gray400-color: #d7dadd;
  --gray300-color: #e5e8ed;
  --gray200-color: #f4f5f6;
  --gray100-color: #f9f9f9;
  --white-05-color: rgb(255 255 255 / 50%);
  --white-015-color: rgb(255 255 255 / 15%);
  --white-012-color: rgb(255 255 255 / 12%);
  --primary01-color: rgb(23 105 106 / 10%);
  --overlay-color: rgb(0 0 0 / 60%);
  --graywhite-color: rgb(255 255 255 / 60%);
  --input-hover-color: rgb(23 105 106 / 40%);
  --button-hover-color: #145c5d;
  --facebook-color: #4267b2;
  --twitter-color: #1da1f2;
  --pinterest-color: #e60023;
  --brown-color: #874e19;
  --blue-gray-color: #c0dded;
  --dark-blue-color: #02457a;
  --green-color: #06623b;
  --yellow-color: #fcd164;
  --red-color: #f75151;
  --beige-color: #d8c1ad;
  --gray-color: #9a9494;
  --orange-color: #fb9620;
  --purple-color: #a27ac9;
  --dummy-white-color: #f1f1f1;
  --pink-color: #ffc0cb;
  --malachite-color: #1fdf58;
  --dandelion-color: #f2ea2b;
  --h1-font: 900 2.875rem / 130% "Lato", sans-serif;
  --h2-font: 900 2rem / 130% "Lato", sans-serif;
  --h3-font: 700 1.75rem / 150% "Lato", sans-serif;
  --h4-font: 700 1.5rem / 130% "Lato", sans-serif;
  --h5-font: 700 1.25rem / 130% "Lato", sans-serif;
  --h6-font: 700 1rem / 150% "Lato", sans-serif;
  --display1: 900 4.5rem / 130% "Lato", sans-serif;
  --display2: 900 4rem / 130% "Lato", sans-serif;
  --display3: 900 3.5rem / 130% "Lato", sans-serif;
  --display4: 900 3rem / 130% "Lato", sans-serif;
  --lead-regular: 400 1.25rem / 150% "Lato", sans-serif;
  --lead-bold: 700 1.25rem / 150% "Lato", sans-serif;
  --large-regular: 400 1.125rem / 150% "Lato", sans-serif;
  --large-bold: 700 1.125rem / 150% "Lato", sans-serif;
  --base-regular: 400 1rem / 160% "Lato", sans-serif;
  --base-bold: 700 1rem / 160% "Lato", sans-serif;
  --small-regular: 400 0.875rem / 150% "Lato", sans-serif;
  --small-bold: 700 0.875rem / 150% "Lato", sans-serif;
  --extrasmall-regular: 400 0.75rem / 150% "Lato", sans-serif;
  --extrasmall-bold: 700 0.75rem / 150% "Lato", sans-serif;
  --extra2small-regular: 400 0.625rem / 150% "Lato", sans-serif;
  --extra2small-bold: 900 0.625rem / 150% "Lato", sans-serif;
  --ubuntu-bold: 700 calc(0.97rem + 0.65vw) / 150% "Ubuntu", sans-serif;
  --divider-gradient: linear-gradient(rgb(218 219 221 / 0%) 0%, rgb(218 219 221 / 50%) 12.33%, #dadbdd 51.91%, rgb(218 219 221 / 50%) 87.85%, rgb(218 219 221 / 0%) 100%);
  --divider2-gradient: linear-gradient(270.01deg, rgb(218 219 221 / 0%) 0%, rgb(218 219 221 / 50%) 12.33%, #dadbdd 51.91%, rgb(218 219 221 / 50%) 87.85%, rgb(218 219 221 / 0%) 100%);
  --app-store-gradient: linear-gradient(0deg, rgb(26 26 26 / 10%) -0.24%, rgb(33 33 33 / 15.1%) 177.23%, rgb(53 53 53 / 22.7%) 444.15%, rgb(87 87 87 / 31.8%) 767.35%, rgb(133 133 133 / 42.1%) 1129.49%, rgb(179 179 179 / 51%) 1442.59%);
  --small-shadow: 0 4px 4px rgb(30 33 44 / 3%), 0 12px 10px rgb(154 156 165 / 4%), 0 30px 24px rgb(154 156 165 / 5%), 0 80px 80px rgb(154 156 165 / 8%);
  --regular-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(154 156 165 / 8%), 0 30px 24px rgb(154 156 165 / 10%), 0 80px 80px rgb(154 156 165 / 16%);
  --large-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(30 33 44 / 8%), 0 26px 24px rgb(30 33 44 / 10%), 0 60px 80px rgb(30 33 44 / 16%);
  --product-shadow: 0 80px 80px -20px rgb(154 156 165 / 16%), 0 30px 24px -10px rgb(154 156 165 / 10%), 0 12px 10px -6px rgb(154 156 165 / 8%), 0 4px 4px -4px rgb(30 33 44 / 5%);
  --main-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  --custom-shadow: 0 8px 4px 4px rgb(0 0 0 / 3%), 0 20px 20px 13px rgb(0 0 0 / 3%);
  --custom-hover-shadow: 0 8px 4px 4px rgb(0 0 0 / 3%), 0 20px 20px 13px rgb(0 0 0 / 3%), 0 20px 20px 13px rgb(0 0 0 / 3%);
  --z-modal: 1000;
  --z-tooltip: 100;
  --z-postfixed: 11;
  --z-fixed: 10;
  --z-prefixed: 9;
  --z-up: 1;
  --z-down: -1;
  --to-right-05-animation: to-right 0.4s ease-in-out forwards 0.5s;
  --to-right-03-animation: to-right 0.4s ease-in-out forwards 0.3s;
  --to-bottom-animation: to-bottom 0.4s ease-in-out forwards 0.5s;
  --scale-in-animation: scale-in 0.4s ease-in-out forwards 0.3s;
  --scale-in-left-animation: scale-in-left 0.4s ease-in-out forwards 0.7s;
  --scale-out-right-animation: scale-out-right 0.4s ease-in-out forwards 0.3s;
}

@keyframes to-right {
  from {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-2.25rem);
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}
@keyframes to-bottom {
  from {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-2.25rem);
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
@keyframes scale-in {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(1.15);
    transform-origin: center;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
@keyframes scale-in-left {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(1.15);
    transform-origin: right center;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
@keyframes scale-out-right {
  from {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.85);
    transform-origin: right top;
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  overflow: hidden;
  width: 1px;
  height: 1px;
}

.overflow-hidden {
  overflow: hidden;
}

.section-title {
  font: var(--h1-font);
  font-size: calc(1.6325rem + 1.04vw);
  text-align: center;
  color: var(--gray900-color);
}

.container {
  margin-inline: 1.25rem;
  max-width: 76.875rem;
}
@media (min-width: 80.625em) {
  .container {
    margin-inline: auto;
  }
}
@media (max-width: 35.9375em) {
  .container {
    margin-inline: 0.9375rem;
  }
}
@media (min-width: 82.8125em) {
  .container--40 {
    margin-inline: auto;
  }
}
@media (max-width: 82.75em) {
  .container--40 {
    margin-inline: 2.5rem;
  }
}
@media (max-width: 61.9375em) {
  .container--40 {
    margin-inline: 1.875rem;
  }
}
@media (max-width: 35.9375em) {
  .container--40 {
    margin-inline: 0.9375rem;
  }
}

.container-wide {
  margin-inline: 1.25rem;
  max-width: 116.25rem;
}
@media (min-width: 120em) {
  .container-wide {
    margin-inline: auto;
  }
}
@media (max-width: 35.9375em) {
  .container-wide {
    margin-inline: 0.9375rem;
  }
}

.input {
  outline: none;
  border: 1px solid var(--gray400-color);
  border-radius: 0.25rem;
  padding: 0.6875rem 1rem;
  font: var(--small-regular);
  color: var(--gray800-color);
}
.input::placeholder {
  font: var(--small-regular);
  color: var(--gray600-color);
}
.input-wrapper {
  position: relative;
}
.input-wrapper::before {
  content: "Please provide a valid input.";
  position: absolute;
  left: 0;
  top: calc(100% + 0.25rem);
  font: var(--extrasmall-regular);
  color: var(--danger-color);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.input-wrapper::after {
  content: "";
  position: absolute;
  right: 0.875rem;
  top: 50%;
  width: 1rem;
  height: 1rem;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%);
  transition: all 0.3s;
}
.input-wrapper--warning::before {
  opacity: 1;
  visibility: visible;
}
.input-wrapper--warning::after {
  background-image: url("../img/warning.svg");
  opacity: 1;
  visibility: visible;
}
.input-wrapper--warning .input {
  border-color: var(--danger-color);
}
.input-wrapper--success::before {
  content: "Looks good!";
  color: var(--success-color);
  opacity: 1;
  visibility: visible;
}
.input-wrapper--success::after {
  background-image: url("../img/success.svg");
  opacity: 1;
  visibility: visible;
}
.input-wrapper--success .input {
  border-color: var(--success-color);
}
.input:focus {
  border-color: var(--input-hover-color);
  box-shadow: var(--main-shadow);
}

.btn {
  display: inline-flex;
  column-gap: 0.5rem;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--primary-color);
  border-radius: 0.25rem;
  padding-inline: 1.5rem;
  height: 2.25rem;
  font: var(--extrasmall-bold);
  letter-spacing: 0.03125rem;
  white-space: nowrap;
  color: var(--white-color);
  background-color: var(--primary-color);
  transition: all 0.3s ease;
}
.btn svg {
  width: 0.75rem;
  height: 0.46875rem;
  fill: currentcolor;
}
.btn:hover {
  background-color: var(--button-hover-color);
}
.btn:active {
  background-color: var(--button-hover-color);
}
.btn--mid {
  column-gap: 0.5rem;
  padding-inline: 2.0625rem;
  height: 2.75rem;
  font: var(--small-bold);
}
.btn--lg {
  padding-inline: 2.5rem;
  height: 3.25rem;
  font: var(--base-bold);
}
.btn--lg svg {
  width: 1.125rem;
  height: 0.6875rem;
}
.btn--outline {
  color: var(--primary-color);
  background-color: transparent;
}
.btn--outline:hover {
  color: var(--white-color);
  background-color: var(--primary-color);
}
.btn--outline:active {
  color: var(--white-color);
  background-color: var(--primary-color);
}
.btn--outline:disabled {
  opacity: 0.5;
}

.btn-app {
  display: inline-block;
  border-radius: 0.5rem;
  padding-left: 3.25rem;
  width: 10.125rem;
  height: 3rem;
  background-repeat: no-repeat;
  background-color: var(--gray800-color);
}
.btn-app__subtitle {
  position: relative;
  top: 0.3125rem;
  display: block;
  font-family: "MyriadPro", sans-serif;
  font-size: 0.75rem;
  color: var(--gray300-color);
}
.btn-app__subtitle--google {
  top: 0.375rem;
  font-size: 0.6875rem;
  text-transform: uppercase;
}
.btn-app__title {
  position: relative;
  top: 0.125rem;
  display: block;
  font-family: "MyriadPro", sans-serif;
  font-weight: 500;
  font-size: 1.375rem;
  color: var(--white-color);
}
.btn-app__title--google {
  top: 0.1875rem;
  font-size: 1.25rem;
}
.btn-app:hover {
  box-shadow: var(--main-shadow);
}
.btn-app--apple {
  position: relative;
  background-image: url("../img/cta-apple.jpg");
  background-position: left 0.889375rem center;
  background-size: 1.556875rem 1.913125rem;
}
.btn-app--apple::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  clip-path: polygon(100% 0, 46.3% 0, 65.43% 100%, 100% 100%, 100% 0);
  width: 100%;
  height: 100%;
  background-image: var(--app-store-gradient);
}
.btn-app--google {
  padding-left: 3.125rem;
  font-family: "Roboto", sans-serif;
  background-image: url("../img/cta-google.jpg");
  background-position: left 0.764375rem center;
  background-size: 1.72875rem 1.93rem;
}

.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  background-color: var(--white-05-color);
  transition: background-color 0.3s;
  cursor: pointer;
}
.arrow svg {
  width: 1.1875rem;
  height: 0.9375rem;
  fill: var(--gray800-color);
  transition: fill 0.3s;
}
.arrow:hover {
  background-color: var(--white-color);
}
.arrow--trans {
  background-color: transparent;
}
.arrow--trans:hover {
  background-color: var(--primary-color);
}
.arrow--trans:hover svg {
  fill: var(--white-color);
}
@media (max-width: 35.9375em) {
  .arrow--trans {
    width: 2.25rem;
    height: 2.25rem;
  }
  .arrow--trans svg {
    width: 0.9375rem;
    height: 0.75rem;
  }
}

.flat-pagination {
  display: none;
  text-align: center;
}
.flat-pagination .swiper-pagination-bullet {
  position: relative;
  overflow: hidden;
  margin: 0 !important;
  border-radius: 0.25rem;
  width: 1.875rem;
  height: 0.1875rem;
  background-color: none;
  opacity: 1;
}
.flat-pagination .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--gray500-color) !important;
  opacity: 1;
}
.flat-pagination .swiper-pagination-bullet::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  display: inline-block;
  width: 0;
  height: 100%;
  background-color: transparent !important;
  opacity: 0;
  transform: translateX(-50%);
  transition: width 0.2s ease-in-out;
}
.flat-pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 0.75rem !important;
}
.flat-pagination .swiper-pagination-bullet-active::after {
  width: 100%;
  background-color: var(--gray900-color) !important;
  opacity: 1;
}
@media (max-width: 47.9375em) {
  .flat-pagination {
    display: block;
    pointer-events: auto;
  }
}

.product {
  position: relative;
}
.product__look {
  position: relative;
  overflow: hidden;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  cursor: pointer;
}
.product__image-wrapper {
  position: relative;
}
.product__image-link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.product__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.product__rating {
  position: absolute;
  right: 1.03125rem;
  top: 1.03125rem;
  display: block;
  cursor: default;
}
.product__rating-icon {
  width: 0.8125rem;
  height: 0.75rem;
  fill: none;
  stroke: var(--gray500-color);
  stroke-width: 10px;
}
.product__rating-icon:not(:last-child) {
  margin-right: -0.1rem;
}
.product__rating-icon--active {
  fill: var(--warning-color);
  stroke: var(--warning-color);
}
.product__favorite {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  background-color: var(--white-color);
}
.product__favorite::before {
  content: "Add to wishlist";
  position: absolute;
  left: -7.1875rem;
  top: 50%;
  display: flex;
  align-items: center;
  border-radius: 0.25rem;
  padding-inline: 0.625rem;
  height: 1.75rem;
  font: var(--small-regular);
  line-height: 1;
  white-space: nowrap;
  text-align: center;
  color: var(--white-color);
  background-color: var(--black-color);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%);
  transition: opacity 0.3s;
}
.product__favorite::after {
  content: "";
  position: absolute;
  left: -0.375rem;
  top: 50%;
  border-style: solid;
  border-width: 0.3125rem 0 0.3125rem 0.46875rem;
  border-color: transparent transparent transparent var(--black-color);
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%);
  transition: opacity 0.3s;
}
.product__favorite svg {
  width: 0.9375rem;
  height: 0.875rem;
  fill: var(--white-color);
  stroke: var(--gray700-color);
  stroke-width: 2.5rem;
  transition: stroke 0.3s, fill 0.3s;
}
.product__favorite:hover::before {
  opacity: 1;
  visibility: visible;
}
.product__favorite:hover::after {
  opacity: 1;
  visibility: visible;
}
.product__favorite--rectangle {
  position: revert;
  border-radius: 0.25rem;
  padding-inline: 2rem;
  width: auto;
  height: 2.75rem;
  letter-spacing: 0.01875rem;
  background-color: var(--white-color);
  gap: 0;
}
.product__favorite--rectangle::before {
  content: none;
}
.product__favorite--rectangle::after {
  content: none;
}
.product__favorite--rectangle svg {
  margin-right: 0.3125rem;
  width: 1.3125rem;
  height: 0.875rem;
  fill: var(--white-color);
  stroke: var(--primary-color);
  stroke-width: 2.5rem;
  transition: stroke 0.3s, fill 0.3s;
}
.product__favorite--rectangle:hover svg {
  stroke: var(--white-color);
}
.product__favorite--cart {
  position: revert;
  border-radius: 0;
  width: auto;
}
.product__favorite--cart::before {
  content: "Move to";
  position: revert;
  margin-right: 0.5rem;
  padding-inline: 0;
  font: var(--extrasmall-bold);
  letter-spacing: 0.03125rem;
  color: var(--gray800-color);
  background-color: transparent;
  opacity: 1;
  visibility: visible;
  transform: none;
  transition: none;
}
.product__favorite--cart::after {
  content: none;
}
.product__favorite--cart svg {
  stroke: var(--gray800-color);
}
.product__favorite--active svg {
  fill: var(--primary-color);
  stroke: var(--primary-color);
}
.product__discount {
  position: absolute;
  left: 1rem;
  top: 1rem;
  display: inline-block;
  border-radius: 0.25rem;
  padding-block: 0.075rem;
  padding-inline: 0.5rem;
  font: var(--base-bold);
  color: var(--white-color);
  background-color: var(--danger-color);
}
.product__info {
  padding-inline: 1rem;
  background-color: var(--gray300-color);
}
.product__info--white {
  background-color: var(--white-color);
}
.product__name {
  display: block;
  overflow: hidden;
  margin-bottom: 0.5rem;
  font: var(--large-regular);
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--gray800-color);
  transition: color 0.3s ease-in-out;
}
.product__name:hover {
  color: var(--primary-color);
}
.product__prices {
  display: flex;
  align-items: center;
}
.product__price {
  display: inline-block;
  font: var(--h5-font);
  color: var(--gray900-color);
}
.product__price--h4 {
  font: var(--h4-font);
}
.product__price--red {
  margin-right: 0.75rem;
  color: var(--danger-color);
}
.product__old-price {
  display: inline-block;
  font: var(--large-regular);
  line-height: 1;
  text-decoration: line-through;
  color: var(--gray700-color);
}
.product__old-price--base {
  font: var(--base-regular);
  line-height: 1;
}
.product__bottom {
  position: relative;
  overflow: hidden;
  padding-bottom: 0rem;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s;
}
.product__bottom--visible {
  overflow: visible;
  padding-bottom: 1rem;
  width: auto;
  height: auto;
  opacity: 1;
  visibility: visible;
}
